import React, { Component } from 'react';
import './App.css';
import Icon from '@material-ui/core/Icon';
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import { Typography, Snackbar } from '@material-ui/core';
import { AppBar, Toolbar, IconButton } from '@material-ui/core';
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import Logo from './../src/logo.svg'

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#b71c1c',
    },
    secondary: {
      main: '#311b92',
    }
  },
});


class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isRuleView: false,
      var: false,
      rul: false,
      quest: false,
      variables: [],
      rules: [],
      variableName: "",
      variableQuest: "",
      variableType: "Entrada",
      textRule: "",
      displayTextRule: "",
      selectedVarIn: "",
      selectedVarOut: "",
      selectedOperatorT: "",
      selectedOperatorL: "",
      test: "",
      listForQuests: [],
      positionForQuest: 0,
      responseText: "",
      filteredJoinedRules: "",
      result: "",
      openSnack: false
    }
  }
  render() {
    return (
      <MuiThemeProvider theme={theme}>
        <div className="App">
          <AppBar position="static" style={{ backgroundColor: "black", display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
            <Toolbar>
              <img src={Logo} alt="logo" style={{ width: "80px" }} />
              <Typography variant="h5" color="inherit"  >
                React Sinta
              </Typography>
            </Toolbar>
            <IconButton>
              <a href="https://github.com/jhersonharyson">
                <i className="fa fa-github" style={{ color: "white", float: "right", right: "-150px" }}></i>
              </a>
            </IconButton>
          </AppBar>
          <div
            style={{ fontSize: "4px", position: "absolute", width: "150px", paddingLeft: "5px", paddingRight: "5px", height: "90%", margin: "0px", color: "black", backgroundColor: "white", display: "flex", flexDirection: "column", justifyContent: "flex-start", alignItems: "center", boxShadow: "0px 0px 3px black" }}
          >
            <Button variant="contained" color="primary" fullWidth style={{ marginTop: "15px" }} onClick={_ => this.setState({ var: true })}>
              <Icon style={{ marginRight: "10px", marginLeft: "0px" }}>add_circle</Icon> variaveis
          </Button>
            <Button variant="contained" color="primary" fullWidth style={{ marginTop: "15px" }} onClick={_ => this.setState({ rul: true })} >
              <Icon style={{ marginRight: "10px", marginLeft: "-10px" }}>add_circle</Icon> regras
          </Button>
            <Button variant="contained" color="default" fullWidth style={{ marginTop: "15px" }} onClick={_ => this.setState({ isRuleView: false })}>
              <Icon style={{ marginRight: "10px", marginLeft: "0px" }}>visibility</Icon> variaveis
          </Button>
            <Button variant="contained" color="default" fullWidth style={{ marginTop: "15px" }} onClick={_ => this.setState({ isRuleView: true })} >
              <Icon style={{ marginRight: "10px", marginLeft: "-10px" }}>visibility</Icon> regras
          </Button>
            <Button variant="contained" color="secondary" fullWidth style={{ marginTop: "15px" }} onClick={_ => this.handleExecutar()}>
              <Icon style={{ marginRight: "10px" }}>play_arrow</Icon> Executar
          </Button>


          </div>
          <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between", paddingLeft: "160px", paddingTop: "25px", paddingRight: "10px" }}>

            {!this.state.isRuleView &&
              <div className="main" style={{ width: "40%" }}>
                <h5>Variáveis de Entrada</h5>
                {this.state.variables.map((e, index) => {
                  if (e.variableType !== "Entrada") return null
                  return (
                    <div key={index} style={{ display: "flex", flexDirection: "row", marginTop: "15px", borderBottom: "1px dashed gray" }}>
                      <div style={{ width: "95%", textAlign: "left" }}>
                        <h6>Variável: {e.variableName}</h6>
                        <h6>Pergunta: {e.variableQuest}</h6>
                      </div>
                      <div>
                        <Button variant="contained" color="secondary" size="small" style={{ alignSelf: "flex-start" }} onClick={_ => {
                          this.setState({ variables: this.state.variables.filter((e, ind) => { return ind !== index }) })
                        }}>
                          <Icon>delete</Icon>
                        </Button>
                      </div>
                    </div>
                  )
                })}
              </div>
            }
            {!this.state.isRuleView &&
              <div className="main" style={{ width: "40%" }}>
                <h5>Variáveis de Saída</h5>
                {this.state.variables.map((e, index) => {
                  if (e.variableType !== "Saida") return null
                  return (
                    <div key={index} style={{ display: "flex", flexDirection: "row", marginTop: "15px", borderBottom: "1px dashed gray" }}>
                      <div style={{ width: "95%", textAlign: "left" }}>
                        <h6>Variável: {e.variableName}</h6>
                        <h6>Valor: {e.variableQuest}</h6>
                      </div>
                      <div>
                        <Button variant="contained" color="secondary" size="small" style={{ alignSelf: "flex-start" }} onClick={_ => this.setState({ variables: this.state.variables.filter((e, ind) => { return ind !== index }) })}>
                          <Icon>delete</Icon>
                        </Button>
                      </div>
                    </div>
                  )
                })}
              </div>
            }
            {this.state.isRuleView &&
              <div style={{ width: "100%" }}>
                <h5>Regras</h5>
                {this.state.rules.map((e, index) => {
                  return (
                    <div key={index} style={{ display: "flex", flexDirection: "row", marginTop: "15px", borderBottom: "1px dashed gray" }}>
                      <div style={{ width: "95%", textAlign: "left" }}>
                        <h6>Regra: {e.rule}</h6>
                        <h6>Função: {e.function}</h6>
                      </div>
                      <div>
                        <Button variant="contained" color="secondary" size="small" style={{ alignSelf: "flex-start" }} onClick={_ => {
                          const filteredJoinedRules = this.state.filteredJoinedRules.split('\n\n').filter(ele => { return (ele !== "" || ele !== null) }).filter((e, ind) => { return ind !== index }).join('')
                          console.log('asdasdasdads');

                          console.log(filteredJoinedRules)
                          this.setState({ filteredJoinedRules })
                          this.setState({ rules: this.state.rules.filter((e, ind) => { return ind !== index }) })
                        }
                        }>
                          <Icon>delete</Icon>
                        </Button>
                      </div>
                    </div>
                  )
                })}
              </div>
            }

          </div>
          <Dialog
            open={this.state.var}
            onClose={_ => this.setState({ var: false })}
            disableBackdropClick
            disableEscapeKeyDown
            aria-labelledby="scroll-dialog-title"
          >
            <DialogTitle id="scroll-dialog-title">Adicionar Variáveis</DialogTitle>
            <DialogContent>

              <FormControl style={{ minWidth: 120 }}>
                <InputLabel htmlFor="tipo">Tipo</InputLabel>
                <Select
                  value={this.state.variableType}
                  onChange={event => this.setState({ variableType: event.target.value })}
                  inputProps={{
                    name: 'Tipo',
                    id: 'tipo',
                  }}
                >
                  <MenuItem value="Entrada">Entrada</MenuItem>
                  <MenuItem value="Saida">Saida</MenuItem>
                </Select>
              </FormControl>
              <TextField fullWidth label="Nome" value={this.state.variableName} onChange={event => this.setState({ variableName: event.target.value })} />
              <TextField fullWidth label={this.state.variableType === "Entrada" ? "Pergunta" : "Valor"} value={this.state.variableQuest} onChange={event => this.setState({ variableQuest: event.target.value })} />
            </DialogContent>
            <DialogActions>
              <Button onClick={_ => this.setState({ var: false })} color="primary">
                Cancelar
            </Button>
              <Button onClick={event => this.addVariables(event)} color="primary">
                <Icon style={{ marginRight: "10px", marginLeft: "0px" }}>add_circle</Icon>ADICIONAR
            </Button>
            </DialogActions>
          </Dialog>

          <Dialog
            open={this.state.rul}
            onClose={_ => this.setState({ rul: false })}
            fullWidth={true}
            maxWidth="md"

            disableBackdropClick
            disableEscapeKeyDown
            aria-labelledby="scroll-dialog-title"
          >
            <DialogTitle id="scroll-dialog-title">Adicionar Regras</DialogTitle>
            <DialogContent>

              <div style={{ display: "flex", flexDirection: "row", alignItems: "baseline", justifyContent: "space-between", }}>
                <div style={{ display: "flex", flexDirection: "row", justifyContent: "flex-start" }}>
                  {this.state.textRule &&
                    <FormControl style={{ marginRight: "15px" }}>
                      <InputLabel htmlFor="operador-logico">Lógico </InputLabel>
                      <Select
                        value={this.state.selectedOperatorL}
                        onChange={event => this.setState({ selectedOperatorL: event.target.value })}
                        fullWidth
                        style={{ width: "100%", minWidth: "100px" }}
                        inputProps={{
                          name: "operador-logico",
                          id: 'operador-logico',

                        }}
                      >
                        <MenuItem value="">
                          <em>Selecione</em>
                        </MenuItem>
                        <MenuItem value="&&">
                          <em>E - (AND)</em>
                        </MenuItem>
                        <MenuItem value="||">
                          <em>OU - (OR)</em>
                        </MenuItem>
                      </Select>
                      <Icon>arrow_right_alt</Icon>
                    </FormControl>
                  }
                  <FormControl style={{ marginRight: "15px" }} >
                    <InputLabel style={{ marginLeft: "2px" }} htmlFor="entrada">Variáveis</InputLabel>
                    <Select
                      value={this.state.selectedVarIn}
                      onChange={(event) => { this.setState({ selectedVarIn: event.target.value }) }}
                      //onChange={event => this.handleChangeTextRule(event)}
                      fullWidth
                      style={{ width: "100px" }}
                      inputProps={{
                        name: "entrada",
                        id: 'var',

                      }}
                    >
                      <MenuItem value="">
                        <em>Selecione</em>
                      </MenuItem>
                      {this.state.variables.map((e, index) => {
                        if (e.variableType !== "Entrada") return null
                        return (<MenuItem key={index} value={index}>{e.variableName}</MenuItem>)
                      })}
                    </Select>
                    <Icon>arrow_right_alt</Icon>
                  </FormControl>

                  <FormControl style={{ marginRight: "15px" }}>
                    <InputLabel htmlFor="operador-teste">Operações</InputLabel>
                    <Select
                      value={this.state.selectedOperatorT}
                      onChange={event => this.setState({ selectedOperatorT: event.target.value })}
                      //onChange={event => this.handleChangeTextRule(event)}
                      fullWidth
                      style={{ width: "100%", minWidth: "100px" }}
                      inputProps={{
                        name: "operador-teste",
                        id: 'operador-teste',
                      }}
                    >
                      <MenuItem value="">
                        <em>{"Selecione"}</em>
                      </MenuItem>
                      <MenuItem value=">">
                        <em>{"MAIOR QUE ( > )"}</em>
                      </MenuItem>
                      <MenuItem value="<">
                        <em>{"MENOR QUE ( < )"}</em>
                      </MenuItem>
                      <MenuItem value="===">
                        <em>{"IGUAL     ( == )"}</em>
                      </MenuItem>
                    </Select>
                    <Icon>arrow_right_alt</Icon>
                  </FormControl>

                  <TextField style={{ width: "25%" }} inputProps={{ width: "50px" }} value={this.state.test} onChange={event => this.setState({ test: event.target.value })} label="Valor" />
                </div>
                <Button size="small" variant="contained" id="a" onClick={event => this.handleChangeTextRule(event, "add")}><Icon>add_outline</Icon></Button>
              </div>

              <div style={{ display: "flex", flexDirection: "row", alignItems: "baseline" }}>
                <span style={{ marginRight: '15px', }}>SE</span>
                <TextField fullWidth value={this.state.displayTextRule} InputProps={{ readOnly: true, }} />
              </div>
              <div style={{ display: "flex", flexDirection: "row", alignItems: "baseline" }}>
                <span style={{ marginRight: '15px', }}>ENTÃO</span>
                <FormControl >
                  <InputLabel htmlFor="saida">Adicionar Variáveis de Saída</InputLabel>
                  <Select
                    value={this.state.selectedVarOut}
                    onChange={event => this.setState({ selectedVarOut: event.target.value })}
                    fullWidth
                    autoWidth
                    style={{ width: "250px" }}
                    inputProps={{
                      name: "saida",
                      id: 'var',
                    }}
                  >
                    <MenuItem value="">
                      <em>Selecione</em>
                    </MenuItem>
                    {this.state.variables.map((e, index) => {
                      if (e.variableType !== "Saida") return null
                      return (<MenuItem key={index} value={index}>{e.variableName}</MenuItem>)
                    })}
                  </Select>
                </FormControl>
              </div>
            </DialogContent>
            <DialogActions>
              <Button onClick={_ => this.setState({ rul: false })} color="primary">
                Cancelar
            </Button>
              <Button variant="contained" onClick={event => this.addRules(event)} color="primary">
                <Icon style={{ marginRight: "10px", marginLeft: "0px" }}>add_circle</Icon>ADICIONAR REGRA
            </Button>
            </DialogActions>
          </Dialog>

          {this.state.listForQuests[this.state.positionForQuest] &&
            <Dialog
              open={this.state.quest}
              onClose={_ => this.setState({ quest: false })}
              disableBackdropClick
              disableEscapeKeyDown
              aria-labelledby="scroll-dialog-title"
            >
              <DialogTitle id="scroll-dialog-title">{this.state.listForQuests[this.state.positionForQuest].variableType !== "Resposta" ? "Perguntas" : "Resposta"}</DialogTitle>
              <DialogContent>
                <Typography variant="h3">{this.state.listForQuests[this.state.positionForQuest].variableQuest}</Typography>
                {
                  !this.state.result ?
                    // (
                    //   this.state.listForQuests[this.state.positionForQuest].variableType === "Resposta" ? <Typography variant="h4" >Resposta Não Classificada</Typography>:
                    <TextField fullWidth label="Resposta" disabled={this.state.listForQuests[this.state.positionForQuest].variableType === "Resposta"} value={this.state.listForQuests[this.state.positionForQuest].variableType === "Resposta" ? "Não Classificada" : this.state.responseText} onChange={event => this.handleChangeTextResponse(event)} />
                    // )
                    :
                    <Typography variant="h4" >{this.state.result}</Typography>
                }
              </DialogContent>
              <DialogActions>
                <Button onClick={_ => this.setState({ quest: false })} color="primary">
                  Cancelar
            </Button>
                <Button onClick={_ => this.handleAvancar()} color="primary">
                  <Icon style={{ marginRight: "10px", marginLeft: "0px" }}>arrow_right_alt</Icon>AVANÇAR
            </Button>
              </DialogActions>
            </Dialog>
          }
          <Snackbar
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'left',
            }}
            open={this.state.openSnack}
            autoHideDuration={6000}
            onClose={_ => this.setState({ openSnack: false })}
            ContentProps={{
              'aria-describedby': 'message-id',
            }}
            message={<span id="message-id">Campos Inválidos!</span>}
          />
        </div>
      </MuiThemeProvider>
    );
  }
  /*-------------------------------------------------*/
  /*------------- adiciona variável ------------------*/
  addVariables = async event => {
    event.preventDefault()
    let { variableName, variableQuest, variableType } = this.state;
    if (variableName === "" || /\s+/.exec(variableName) || variableQuest === "")
      return this.setState({ openSnack: true })



    let variables = this.state.variables
    variableQuest = (variableType === "Saida") ? '\'' + variableQuest + '\'' : variableQuest
    variables.push({ variableName, variableQuest, variableType })
    await this.setState({ variables });
    await this.setState({ variableName: "", variableQuest: "", variableType });
  }
  handleChangeTextRule = async (event, type) => {
    event.preventDefault()
    event.stopPropagation()

    // console.log(event)
    // @||@ variáveis
    // #||# Operadores Logico
    // $||$ Operadores Teste
    // !||! Operador Negação
    // *||* Valor de Teste 
    // %||% Valor de saida
    const { value } = event.target
    if (value === "") return

    if (type === "add") {
      let text = "";
      if (this.state.textRule) {
        if (this.state.selectedOperatorL === "")
          return this.setState({ openSnack: true })
        text = " #|" + this.state.selectedOperatorL + "|#"
      }
      const variable = this.state.selectedVarIn;
      const operatorT = this.state.selectedOperatorT;


      const test = this.state.test;

      if (variable === "" || operatorT === "" || test === "") return this.setState({ openSnack: true })
      if (!parseFloat(test))
        return this.setState({ openSnack: true })

      await this.setState({ textRule: this.state.textRule + text + " @|" + this.state.variables[variable].variableName + "|@ $|" + operatorT + "|$ *|" + test + "|*" });
      await this.setState({ selectedOperatorL: "", selectedOperatorT: "", selectedVarIn: "", test: "", isRuleView: false })
      await this.setState({ displayTextRule: this.state.textRule.split('@|').join('').split('|@').join('').split('#|').join('').split('|#').join('').split('$|').join('').split('|$').join('').split('!|').join('').split('|!').join('').split('*|').join('').split('|*').join('').split('%|').join('').split('|%').join('') })
    }
    const endsWith = this.state.textRule.endsWith('|#')
    await this.setState({ endsWith })

    // const filtered = this.state.textRule.split(' ');
    // console.log(filtered)
    // const len = filtered.length
    // let operationInIsPair = 1
    // for(let i = 0; i < len ; i++){
    //   if(/(@\|.*?\|@)/.test(filtered[i])){
    //     operationInIsPair+=1;
    //   }
    // }
    // operationInIsPair = (operationInIsPair % 2 === 0)
    // console.log(operationInIsPair)

    // this.setState({operationIsPair: this.state.textRule.length === 0 ? })


  }
  addRules = async event => {

    event.preventDefault();
    // @||@ variáveis
    // #||# Operadores Logico
    // $||$ Operadores Teste
    // !||! Operador Negação
    // *||* Valor de Teste 
    // %||% Valor de saida
    let { textRule, rules } = this.state;
    textRule += " %|" + this.state.selectedVarOut + "|%"
    textRule = textRule.split(' ')
    let len = textRule.length
    // eslint-disable-next-line
    let vars = [], test = [], opers = [], varst = [], saida = ""
    // test.push('') // ignora na primeira iteração

    for (let i = 0; i < len; i++) {
      if (textRule[i].startsWith('@|') && textRule[i].endsWith('|@'))
        vars.push(textRule[i].replace('@|', '').replace('|@', '')) // variavel
      else if (textRule[i].startsWith('$|') && textRule[i].endsWith('|$'))
        test.push(textRule[i].replace('$|', '').replace('|$', '')) // teste == > <
      else if (textRule[i].startsWith('#|') && textRule[i].endsWith('|#'))
        opers.push(textRule[i].replace('#|', '').replace('|#', '')) // logico
      else if (textRule[i].startsWith('*|') && textRule[i].endsWith('|*'))
        varst.push(textRule[i].replace('*|', '').replace('|*', '')) // valor
      else if (textRule[i].startsWith('%|') && textRule[i].endsWith('|%')) {
        const indexFromOut = textRule[i].replace('%|', '').replace('|%', '')
        saida = textRule[i].replace('%|', ' return ').replace('|%', '').replace(indexFromOut, this.state.variables[indexFromOut].variableName) // saida
      }
    }

    // console.log(vars)
    // console.log(test)
    // console.log(opers)
    // console.log(saida)
    // console.log(varst)

    len = varst.length
    // let out = "(function(){ if( "
    let out = "if( "

    for (let i = 0; i < len; i++) {
      out += vars[i] + " " + test[i] + " " + varst[i] + " " + (opers[i] || "") + " ";
    }
    out += ")"
    // out += saida + ";"
    out += saida + ";\n"
    // out += "}())"

    rules.push({ rule: this.state.textRule, function: out })
    await this.setState({ rules })
    await this.setState({ textRule: "", selectedOperatorL: "", selectedOperatorT: "", selectedVarIn: "", selectedVarOut: "", rul: false, isRuleView: true })

    let filteredJoinedRules = ""
    this.state.rules.forEach(ele => {
      filteredJoinedRules += ele.function + "\n\n"
    })

    this.setState({ filteredJoinedRules })

    //console.log(this.state.filteredJoinedRules)
    // const varIn = (out);
    // console.log(varIn)

  }
  handleExecutar = async _ => {
    let filtered = []
    const variables = this.state.variables;
    const len = variables.length
    for (let i = 0; i < len; i++) {
      if (variables[i].variableType === "Entrada") {
        filtered.push(variables[i])
      }
    }

    filtered.push({ variableType: "Resposta", variableName: "", variableQuest: "", response: "" })
    await this.setState({ listForQuests: filtered, positionForQuest: 0 })
    await this.setState({ quest: true })

  }

  handleChangeTextResponse = async event => {

    const { value } = event.target

    await this.setState({ responseText: value })

    let listForQuests = this.state.listForQuests
    let listAtPosition = this.state.listForQuests[this.state.positionForQuest];

    listAtPosition = {
      ...listAtPosition,
      response: value
    }
    console.log(listAtPosition)

    listForQuests[this.state.positionForQuest] = listAtPosition;

    await this.setState({ listForQuests })
  }

  handleAvancar = async _ => {
    console.log(this.state.listForQuests)
    await this.setState({ positionForQuest: this.state.positionForQuest + 1, responseText: "" })
    const len = this.state.listForQuests.length
    if (this.state.positionForQuest > len - 1) {
      this.setState({ result: "" })
      return
    }
    if (this.state.positionForQuest >= len - 1) {
      let defineVariable = "";
      console.log(this.state.listForQuests)
      let numberOfOut = 0
      for (let i = 0; i < this.state.listForQuests.length - 1; i++) {
        defineVariable += "var " + this.state.listForQuests[i].variableName + " = " + this.state.listForQuests[i].response + ";\n"
      }
      for (let i = 0; i < this.state.variables.length; i++) {
        if (this.state.variables[i].variableType === "Saida") {
          defineVariable += "var " + this.state.variables[i].variableName + " = " + this.state.variables[i].variableQuest + ";\n"
          numberOfOut += numberOfOut + 1;
        }
      }
      console.log(defineVariable + "\n" + this.state.filteredJoinedRules)

      // console.log(eval(' if(1 === 1) result = 5 '))
      // console.log(result)
      // let out = "(function(){ if( "
      // out += saida + ";"
      // out += "}())"
      const result = eval(defineVariable + "\n (function(){" + this.state.filteredJoinedRules + "}())")

      this.setState({ result });


    }
  }

}


export default App;
